
import firebase from "firebase/compat/app";
import "firebase/compat/auth";
import "firebase/compat/firestore";
import "firebase/compat/storage";
import React, { useState } from 'react';
import {Button, InputGroup, FormControl, Modal} from 'react-bootstrap'

// eslint-disable-next-line react-hooks/rules-of-hooks
const firebaseConfig = {
    apiKey: "AIzaSyALiOLWA6WfSmIQmPF3B7_xCEyFvYle874",
    authDomain: "yewon-39800.firebaseapp.com",
    projectId: "yewon-39800",
    storageBucket: "yewon-39800.appspot.com",
    messagingSenderId: "640984105983",
    appId: "1:640984105983:web:030128ce0251fca924ddca"
}
// Initialize Firebase
firebase.initializeApp(firebaseConfig);


function App() {
    const [yewon, setYewon] = useState("")
    const [answer, setAnswer] = useState("")
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const Change = (event) => {
        const {
            target: {value},
        } = event;
        setYewon(value);
    }
    const GetQna = () => {
        const db = firebase.firestore();
        db.collection('Qna').get().then((t) => {
            t.forEach(ele => {
                const data = ele.data();
                if (data[yewon] === undefined) {
                    setAnswer("정보 없음 ㅋ")
                    setModalIsOpen(true)
                } else {
                    setAnswer(data[yewon])
                    setModalIsOpen(true)
                }
            })
        })
    }
    return (
        <div className="App">
            <InputGroup style={{marginTop: "1%", height: "60px", marginLeft: "5%", width: "90%"}} className="mb-3">
                <FormControl
                    onChange={Change}
                    placeholder="질문작성"
                />
                <Button variant="outline-secondary" style={{width: "10%"}} onClick={GetQna}>
                    확인
                </Button>
            </InputGroup>
            <Modal
                size="lg"
                show={modalIsOpen}
                onHide={() => setModalIsOpen(false)}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                        답
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>{answer}</Modal.Body>
            </Modal>
        </div>
    );
}

export default App;
